.lk-user
  padding-top: 0 !important
  margin-bottom: 30px
  .lk-user-header
    padding: 100px 0 20px
    color: #fff
    background-repeat: no-repeat
    background-position: center
    background-size: cover
    @media (min-width: 1024px)
      padding: 150px 0 50px
    &__title
      font-family: Montserrat, sans-serif
      @include adaptive-value('font-size', 40, 24, 1)
      @include adaptive-value('line-height', 40, 24, 1)
      font-weight: 500
  .lk-user-info
    padding-top: 20px
    @media (min-width: 1024px)
      padding-top: 60px
    &__content
      @media (min-width: 768px)
        display: flex
        align-items: stretch

      &__left-side
        margin-bottom: 30px
        @media (min-width: 768px)
          flex: 0 0 calc(50% - 20px)
          max-width: calc(50% - 20px)
          margin: 0 auto 0 0
      &__right-side
        @media (min-width: 768px)
          flex: 0 0 calc(50% - 20px)
          max-width: calc(50% - 20px)
  .lk-user-notify
    padding-top: 20px
  .lk-user-favorites
    padding-top: 20px
    &__content
      margin-top: 20px
      @media (min-width: 768px)
        display: flex
        flex-wrap: wrap
      .enterprises__list__item
        border: 1px solid #ccc !important
        @media (min-width: 960px)
          flex: 1 1 calc(50% - 10px)
          max-width: calc(50% - 10px)
          margin: 0 20px 20px 0

          &:nth-of-type(2n)
            margin-right: 0
          .item-content
            .item-image
              flex: 0 0 150px
              max-width: 150px
            .item-descr
              flex: 1 1 calc(100% - 150px)
              max-width: calc(100% - 150px)
      .btn-row
        @media (min-width: 768px)
          flex: 1 1 100%
          max-width: 100%
          width: 100%
      .see-more
        @media (min-width: 576px)
          max-width: 280px
          margin: 20px auto 0
  form
    .input-group
      margin-bottom: 20px
      &.form-name
        display: block
        width: 100%
        border-bottom: 1px solid #e6e6e6
        padding-bottom: 5px
        span
          text-transform: uppercase
          color: #999
          font-size: 20px
          line-height: 20px
      &.check-group
        margin-bottom: 15px
        input
          width: 0
          height: 0
          position: absolute
          visibility: hidden
          opacity: 0
          &:checked + label
            &:before
              background-color: $accent-color
              border-color: $accent-color
            &:after
              opacity: 1
        label
          display: inline-flex
          align-items: flex-start
          justify-content: flex-start
          position: relative
          line-height: 16px
          &:before
            content: ''
            display: inline-block
            width: 18px
            height: 18px
            flex: 0 0 18px
            border: 1px solid #000
            margin-right: 10px
            transition: background-color .3s, border-color .3s
          &:after
            font-family: Fontello
            font-style: normal
            font-weight: normal
            speak: never
            display: inline-block
            margin: 0 auto
            text-decoration: inherit
            text-align: center
            font-variant: normal
            text-transform: none
            content: '\e809'
            width: 14px
            height: 14px
            position: absolute
            left: 2px
            top: 2px
            font-size: 14px
            line-height: 14px
            color: #fff
            opacity: 0
            transition: opacity .3s
      &.btn-group
        @media (min-width: 576px)
          max-width: 280px
      &.dlabel
        input
          border-bottom: 1px solid #e6e6e6
          color: #000
        input[type="date"]
          color: transparent
          &:focus
            color: #000
          &:valid
            color: #000
