.full-page
  min-height: calc(100vh - 110px)
  &__content
    min-height: calc(100vh - 110px)
.single-page
  min-height: calc(100vh - 183px - 110px)
  &__content
    min-height: calc(100vh - 183px - 110px)

.thin-header
  background-repeat: no-repeat
  background-position: center
  background-size: cover
  background-color: #4e4846
  @media (min-width: 1024px)
    padding-top: 100px !important
    padding-bottom: 20px
  &__title
    color: #fff
    font-family: Montserrat, sans-serif
    font-weight: 600
    text-transform: uppercase
    @include adaptive-value('font-size', 42, 19, 1)
    @include adaptive-value('line-height', 42, 20, 1)
  .search-form
    margin: 10px 0 15px
    @media (min-width: 360px)
      margin: 20px 0
    .input-group
      background-color: rgba(255,255,255,.9)
      display: flex
      button
        display: flex
        align-items: center
        justify-content: center
        border: none
        background-color: transparent
        font-size: 28px
        line-height: 20px
        padding: 0 10px
      input
        flex: 1 1 100%
        border: none
        border-radius: 0
        background-color: transparent
        padding: 10px
        font-size: 18px
        line-height: 20px
        font-weight: 100
        @include input-placeholder
          opacity: 1
          transition: opacity 0.3s ease
        &:focus
          @include input-placeholder
            opacity: 0
            transition: opacity 0.3s ease
.section-title
  text-transform: uppercase
  font-family: Montserrat, sans-serif
  font-weight: 600
  @include adaptive-value('font-size', 35, 20, 1)
  @include adaptive-value('line-height', 40, 24, 1)

.breadcrumbs
  padding: 15px 0
  .breadcrumbs-list
    display: flex
    flex-wrap: wrap
    flex: 1 1 100%
    font-size: 15px
    line-height: 20px
    @media (min-width: 1200px)
      font-size: 16px
    &__item
      position: relative
      margin-bottom: 5px
      margin-right: 10px
      &:before
        content: '/'
        display: inline-block
        margin: 0 10px 0 0
      &:nth-of-type(1)
        &:before
          display: none
    &__link
      transition: color .3s
      &:hover
        color: $accent-color

.catalog-menu
  background-color: $txt-color
  padding: 15px 0
  color: #999
  position: relative
  &:after
    content: ""
    position: absolute
    top: 0
    left: 50%
    width: 100vw
    right: 50%
    height: 100%
    background-color: #1a1a1a
    transform: translateX(-50%)
    z-index: 0
  @media (min-width: 1024px)
    padding: 8px 0
  .container
    position: relative
    z-index: 1
  .catalog-link
    display: flex
    flex: 1 1 100%
    text-transform: uppercase
    font-family: Montserrat, sans-serif
    font-size: 18px
    line-height: 20px
    letter-spacing: .5px
    color: #fff
    @media (min-width: 1024px)
      display: none
    .icon
      margin-right: 15px
      font-size: 23px
      line-height: 20px
      &.icon-down-open-mini
        margin: 0 0 0 auto
        font-size: 24px
        line-height: 20px
        transition: transform .3s
    &.open
      .icon.icon-down-open-mini
        transform: scale(1,-1)
  &__list
    @media (min-width: 1024px)
      display: flex !important
      flex-wrap: wrap
      justify-content: flex-start
  &__item
    padding: 8px 10px
    border-bottom: 1px solid #222
    margin: 0 -10px
    @media (min-width: 1024px)
      margin: 0 0 0 15px
      border-bottom: none
    &:nth-of-type(1)
      margin-top: 15px
      @media (min-width: 1024px)
        margin-top: 0
        margin-left: 0
        padding-left: 0
  &__link
    display: block
    font-family: Montserrat, sans-serif
    transition: color .3s
    font-size: 16px
    line-height: 20px
    &:hover
      color: #fff

.product-item
  &.column-view
    display: flex
    flex-direction: column
    flex: 0 1 calc(50% - 5px)
    max-width: calc(50% - 5px)
    margin: 0 10px 10px 0
    &:nth-of-type(2n)
      margin-right: 0
    @media (min-width: 768px)
      flex: 0 1 calc(33.3% - 6px)
      margin: 0 9px 9px 0
      &:nth-of-type(2n)
        margin-right: 9px
      &:nth-of-type(3n)
        margin-right: 0
    .image-block
      position: relative
      .item-image
        display: block
        width: 100%
        max-width: 100%
        margin: 0
      .stickers
        display: flex
        flex-wrap: wrap
        flex: 1 1 calc(100% - 20px)
        max-width: calc(100% - 20px)
        position: absolute
        top: 10px
        left: 10px
        z-index: 1
        @media (min-width: 1025px)
          flex: 1 1 calc(100% - 50px)
          max-width: calc(100% - 50px)
        &__item
          margin: 0 0 6px 6px
          border-radius: 30px
          color: #fff
          padding: 5px 10px
          font-size: 12px
          line-height: 14px
          font-weight: 600
          @media (min-width: 360px)
            font-size: 13px
          &:nth-of-type(1)
            margin-left: 0
      .item-link
        display: block
        position: relative
        &:after
          content: ''
          display: block
          width: 100%
          height: 100%
          position: absolute
          top: 0
          left: 0
          border: 1px solid #ccc
          border-bottom: none
          transition: border-color .3s
      @media (min-width: 1025px)
        .additem-btns
          display: block !important
          overflow: hidden
          position: absolute
          top: 10px
          right: 10px
          z-index: 1
          max-height: 0
          transition: max-height .5s
          &__list
            display: flex
            flex-direction: column
            align-items: center
            justify-content: center
            background-color: #fff
            border: 1px solid #ccc
            padding: 5px
            border-radius: 3px
          &__link
            display: block
            font-size: 18px
            line-height: 18px
            color: rgba($txt-color, .5)
            margin-bottom: 10px
            transition: color .3s
            &.active
              color: $accent-color
            &:hover
              color: $accent-color
            &:last-child
              margin-bottom: 0
    .descr-block
      display: flex
      flex-direction: column
      flex: 1 1 100%
      align-items: flex-start
      justify-content: space-between
      padding: 10px
      border: 1px solid #ccc
      border-top: none
      transition: border-color .3s
      position: relative
      .item-info
        display: flex
        width: 100%
        flex-wrap: wrap
        .item-marks
          display: flex
          align-items: center
          .item-rating
            color: $accent-color
            font-size: 14px
            line-height: 14px
            margin-bottom: -2px
            @media (min-width: 576px)
              font-size: 16px
              line-height: 16px
          .item-answers
            color: rgba($txt-color, .5)
            display: inline-block
            margin-left: 8px
            line-height: 12px
            font-size: 14px
            @media (min-width: 576px)
              font-size: 16px
              line-height: 16px
        .item-name
          display: inline-block
          font-weight: 600
          font-size: 16px
          line-height: 18px
          margin: 10px 0
          transition: color .3s
          @media (min-width: 576px)
            font-size: 18px
            line-height: 20px
        .item-price
          display: flex
          flex-wrap: wrap
          flex: 1 1 100%
          span
            flex: 0 1 auto
            font-size: 20px
            line-height: 20px
            font-weight: 700
            @media (min-width: 576px)
              font-size: 22px
              line-height: 22px
            &:nth-of-type(1)
              margin-right: 10px
            &.item-oldprice
              text-decoration: line-through
              color: rgba($txt-color, .5)
            &.item-price
              color: $txt-color
            &.item-oldprice + .item-price
              color: $accent-color
        .item-available
          flex: 1 1 100%
          margin: 10px 0
          @media (min-width: 1025px)
            display: flex !important
          span
            display: flex
            align-items: center
            justify-content: flex-start
            font-size: 16px
            line-height: 16px
            .icon
              margin-right: 5px
              margin-bottom: -1px
            &.ok
              color: green
            &.not
              color: rgba($txt-color, .5)
      .addcart-form
        width: 100%
        align-items: stretch
        margin-top: 10px
        @media (min-width: 1025px)
          display: flex !important
        .input-group
          flex: 1 0 auto
          &.count-group
            display: flex
            align-items: stretch
            flex: 0 0 auto
            margin-right: 15px
            .count-input
              display: block
              flex: 1 1 auto
              width: 46px
              max-width: 46px
              text-align: center
              border: 1px solid rgba($txt-color, .3)
            .btns
              display: flex
              flex-direction: column
              .count-btn
                display: flex
                align-items: center
                justify-content: center
                border: 1px solid rgba($txt-color, .3)
                border-left: none
                flex: 1 1 100%
                padding: 0 6px
                color: $txt-color
                transition: color .3s
                &:hover
                  color: $accent-color
                &:nth-of-type(1)
                  border-bottom: none
          &.btn-group
            flex: 1 1 auto
            button
              margin-bottom: 0
              padding: 12px
      .item-properties
        position: absolute
        background-color: #fff
        border: 1px solid transparent
        max-height: 0
        transform: translateY(0px)
        transition: max-height .5s, border-color .3s, transform .5s
        overflow: hidden
        top: calc(100% + 1px)
        left: -1px
        width: calc(100% + 2px)
        z-index: 2
        @media (min-width: 1025px)
          display: block !important
        .main-properties
          padding: 10px 15px
          border-bottom: 1px solid #ccc
          .properties-name
            display: inline-block
            color: #000
            font-weight: 500
            font-size: 15px
            line-height: 24px
            margin-right: 10px
            min-width: 60px
            text-align: right
          .properties-value
            display: inline-flex
            align-items: center
            justify-content: center
            flex: 0 0 auto
            min-width: 30px
            border: 1px solid #ccc
            text-align: center
            margin-left: 10px
            padding: 3px
            &.active
              border-color: $accent-color
            &.property-color
              display: inline-block
              border: 4px solid #fff
              border-radius: 50%
              height: 30px
              position: relative
              &.active
                &:before
                  content: ''
                  display: block
                  width: 28px
                  height: 28px
                  border: 1px solid $accent-color
                  border-radius: 50%
                  position: absolute
                  top: -3px
                  left: -3px
            &:nth-of-type(1)
              margin-left: 0
          &__item
            display: flex
            flex-wrap: wrap
            align-items: center
            margin-bottom: 10px
            &:last-child
              margin-bottom: 0
        .properties-list
          padding: 10px 15px
          &__item
            display: flex
            align-items: center
            justify-content: space-between
            border-bottom: 1px dotted rgba($txt-color, .5)
            font-size: 14px
            line-height: 20px
            margin-bottom: 10px
            span
              display: inline-block
              background-color: #fff
              margin-bottom: -5px
              &:nth-of-type(1)
                padding-right: 3px
                color: rgba($txt-color, .5)
              &:nth-of-type(2)
                padding-left: 3px
    &:hover
      .image-block
        .additem-btns
          max-height: 100px
        .item-link
          &:after
            border-color: $accent-color
      .descr-block
        border-color: $accent-color
        .item-info
