            @media (min-width: 340px)
              font-size: 18px
              line-height: 18px
            @media (min-width: 576px)
              font-size: 20px
              line-height: 20px
            @media (min-width: 640px)
              font-size: 22px
              line-height: 22px
            @media (min-width: 960px)
              font-size: 24px
              line-height: 24px
            &:nth-of-type(1)
              margin-right: 10px
            &.item-oldprice
              text-decoration: line-through
              color: rgba($txt-color, .5)
            &.item-price
              color: $txt-color
            &.item-oldprice + .item-price
              color: $accent-color
        .addcart-form
          width: 100%
          display: flex
          align-items: stretch
          @media (min-width: 640px)
            margin-top: 10px
          @media (min-width: 768px)
            justify-content: flex-end
          .input-group
            flex: 1 0 auto
            &.count-group
              display: flex
              align-items: stretch
              flex: 0 0 auto
              margin-right: 10px
              .count-input
                display: block
                flex: 1 1 auto
                width: 35px
                max-width: 35px
                text-align: center
                border: 1px solid rgba($txt-color, .3)
                font-size: 14px
                line-height: 20px
                @media (min-width: 1024px)
                  width: 38px
                  max-width: 38px
                  font-size: 16px
              .count-btn
                display: flex
                align-items: center
                justify-content: center
                border: 1px solid rgba($txt-color, .3)
                flex: 1 1 100%
                padding: 0 8px
                color: $txt-color
                transition: color .3s
                @media (min-width: 640px)
                  padding: 0 10px
                @media (min-width: 960px)
                  padding: 0 12px
