            padding: 8px
            border-left: 1px solid #ccc
            &:nth-of-type(1)
              margin-left: 0
              border-left: none
            &.active
              color: $accent-color
            &:hover
              color: $accent-color
    &:hover
      .descr-block
        .item-info
          .item-name
            color: $accent-color

.pagination
  margin: 15px 0
  flex: 1 1 100%
  .pagination-list
    display: flex
    justify-content: center
    flex: 1 1 100%
    &__item
      flex: 0 0 15px
      height: auto
      width: 15px
      margin: 0 10px
    &__link
      display: flex
      align-items: center
      justify-content: center
      text-align: center
      color: $txt-color
      transition: color .3s
      font-size: 16px
      line-height: 24px
      .icon
        font-size: 24px
        line-height: 24px
      &:hover
        color: $accent-color
      &.active
        color: $accent-color
        font-weight: 700
        font-size: 20px
        line-height: 24px

.product-filters-block
  .ty-price-slider
    padding: 10px 0
    .ty-price-slider__inputs
      display: flex
      align-items: center
      justify-content: flex-start
    .ty-price-slider__bidi-container
      display: flex
      align-items: center
      justify-content: flex-start
      &:nth-of-type(2)
        span.ty-price-slider__filter-prefix
          display: none
    span.ty-price-slider__filter-prefix
      margin-right: 5px
    input.ty-price-slider__input-text
      border: 1px solid #aaa
      border-radius: 0
      padding: 5px 10px
    .ty-range-slider .ui-slider-handle
      top: -10px
      margin-left: -10px
      width: 20px
      height: 20px
      background-color: #fe323d
      border-radius: 0px
      border: 0px
      cursor: col-resize
      border-radius: 50%
      transition: background-color .3s
      &:hover
        background-color: #a61925



  .list-group-item
    display: block
    border-bottom: 1px solid #eee
    padding: 10px 0
    &:nth-of-type(1)
      margin-top: 0
    &:last-child
      border-bottom: none
    .filter-item-name
      display: flex
      flex-direction: row-reverse
      justify-content: space-between
      cursor: pointer
      .product-filters-title
        font-size: 18px
        line-height: 20px
        font-weight: 600
      .product-filter-switch-icon
        font-size: 20px
        line-height: 20px
        transform: scale(1,1)
        transition: transform .3s
        margin-right: 1px
      &.open
        .product-filter-switch-icon
          transform: scale(1,-1)
    .product-filter
      padding: 15px 0 0
      .form-group
        margin-bottom: 15px
        input
          display: block
          width: 100%
          border: 1px solid #aaa
          border-radius: 0
          padding: 5px 10px
          @include input-placeholder
            opacity: 1
            transition: opacity 0.3s ease
          &:focus
            @include input-placeholder
              opacity: 0
              transition: opacity 0.3s ease
      .product-filters-item-more
        li.checkbox
          margin-bottom: 5px
          &:last-child
            margin-bottom: 0
          input
            position: absolute
            visibility: hidden
            height: 0px
            width: 0px
            opacity: 0
          label
            display: inline-flex
            align-items: center
            justify-content: flex-start
            position: relative
            line-height: 16px
            &:before
              content: ''
              display: inline-block
              width: 18px
              height: 18px
              border: 1px solid $txt-color
              margin-right: 10px
              transition: background-color .3s, border-color .3s
            &:after
              font-family: Fontello
              font-style: normal
              font-weight: normal
              speak: never
              display: inline-block
              margin: 0 auto
              text-decoration: inherit
              text-align: center
              font-variant: normal
              text-transform: none
              content: '\e809'
              width: 14px
              height: 14px
              position: absolute
              left: 2px
              top: 2px
              font-size: 14px
              line-height: 14px
              color: #fff
              opacity: 0
              transition: opacity .3s
          &.active
            label
              &:before
                background-color: $accent-color
                border-color: $accent-color
              &:after
                opacity: 1
        .product-filters-no-items-found
          font-size: 14px
          line-height: 20px
          margin-top: 10px
          color: $accent-color
          &.hidden
            display: none
      &.color-list
        .product-filters-item-more
          li.checkbox
            label
              line-height: 22px
              &:before
                display: none
              &:after
                font-size: 14px
                line-height: 14px
                top: 4px
                left: 4px
              &.icon-inverted
                &:after
                  color: $txt-color
            .color-icon
              display: inline-block
              width: 22px
              height: 22px
              border-radius: 50%
              box-shadow: inset 0 0 10px rgb(0 0 0 / 10%)
              margin-right: 10px
      &.price-filter
        .ty-price-slider__inputs
          display: flex
          flex: 1 1 auto
          max-width: 100%
          flex-wrap: wrap
          justify-content: flex-start
          width: auto
        .ty-price-slider__bidi-container
          display: flex
          flex: 0 0 auto
          width: auto
          max-width: 50%
          align-items: center
          justify-content: flex-start
          &:nth-of-type(1)
            margin-right: 15px
          .ty-price-slider__filter-prefix
            display: inline-block
            margin-right: 5px
            font-size: 16px
            line-height: 24px
            color: #aaa
          .ty-price-slider__input-text
            background-color: transparent
            border: none
            padding: 0 5px
            display: inline-block
            width: auto
            flex: 0 0 auto
            font-size: 18px
            line-height: 24px
            max-width: 90px
            @media (min-width: 1024px)
              max-width: 75px
              font-size: 16px
          .ty-price-slider__filter-postfix
            display: inline-block
            margin-left: 5px
            font-size: 16px
            line-height: 24px
            color: #aaa

.btn-border-style
  display: block
  width: 100%
  border: 1px solid $txt-color
  background-color: transparent
  padding: 15px 0
  text-align: center
  color: #fff
  position: relative
  overflow: hidden
  transition: border-color .3s, color .3s
  margin-bottom: 15px
  text-transform: uppercase
  &.white-style
    border-color: #fff
  &.black-style
    color: $txt-color
  span
    position: relative
    z-index: 2
  &:before
    content: ''
    display: block
    width: 100%
    height: 100%
    position: absolute
    background-color: $accent-color
    top: 0
    left: 0
    transform: translateY(100%)
    transition: transform .3s
  &:hover
    border-color: $accent-color
    color: #fff
    &:before
      transform: translateY(0)


/* contacts form with dinamic-label */
.input-row
  display: flex
  flex-wrap: wrap
.input-group
  &.dlabel
    flex: 1 1 auto
    margin: 20px 0
    position: relative
    input,textarea
      background-color: transparent
      border: none
      border-bottom: 1px solid #fff
      display: block
      width: 100%
      font-size: 16px
      line-height: 20px
      padding: 10px 10px
      color: #fff
      transition: border-color .3s
      &:focus,&:valid
        border-color: $accent-color
    label
      position: absolute
      top: 10px
      left: 0
      transition: all .3s
      span
        color: $accent-color
    textarea
      max-width: 100%
      min-height: 41px
    input:focus + label
      @include adaptive-value('font-size', 14, 12, 1)
      @include adaptive-value('line-height', 14, 12, 1)
      transform: translateY(calc(-100% - 10px))
    textarea:focus + label
      @include adaptive-value('font-size', 14, 12, 1)
      @include adaptive-value('line-height', 14, 12, 1)
      transform: translateY(calc(-100% - 10px))
    input:valid + label
      @include adaptive-value('font-size', 14, 12, 1)
      @include adaptive-value('line-height', 14, 12, 1)
      transform: translateY(calc(-100% - 10px))
    textarea:valid + label
      @include adaptive-value('font-size', 14, 12, 1)
      @include adaptive-value('line-height', 14, 12, 1)
      transform: translateY(calc(-100% - 10px))
  &.checkgroup
    margin: 30px 0
    input
      width: 0px
      height: 0px
      position: absolute
      visibility: hidden
      opacity: 0
    label
      display: inline-flex
      position: relative
      span
        color: $accent-color
        display: inline-block
        margin-left: 3px
      &:before
        content: ''
        display: inline-block
        align-items: center
        justify-content: center
        flex: 0 0 20px
        width: 20px
        height: 20px
        border: 1px solid #fff
        margin-right: 10px
        transition: border-color .3s
      &:after
        font-family: Fontello
        font-style: normal
        font-weight: normal
        speak: never
        display: inline-block
        margin: 0 auto
        text-decoration: inherit
        text-align: center
        font-variant: normal
        text-transform: none
        content: '\e809'
        width: 16px
        height: 16px
        position: absolute
        left: 2px
        top: 2px
        font-size: 14px
        line-height: 16px
        color: #fff
        opacity: 0
        transition: opacity .3s
    input:checked + label
      &:before
        background-color: $accent-color
        border-color: $accent-color
      &:after
        opacity: 1

