.company-page
  background-color: #1a1a1a
  &__content
    color: #fff
    @media (min-width: 960px)
      display: flex
      align-items: stretch
    &__left-side
      @media (min-width: 960px)
        flex: 0 0 35%
        width: 100%
        max-width: 35%
        padding-right: 15px
      @media (min-width: 1024px)
        padding-top: 15px
    &__right-side
      max-width: calc(100% + 30px)
      width: calc(100% + 30px)
      margin: 0 -15px
      @media (min-width: 576px)
        max-width: calc(100% + 60px)
        width: calc(100% + 60px)
        margin: 0 -30px
      @media (min-width: 960px)
        flex: 0 0 calc(65% + 15px)
        width: 100%
        max-width: calc(65% + 15px)
        margin: 0 -15px 0 0
      .company-slider
        .owl-nav
          position: absolute
          width: calc(100% - 30px)
          height: 1px
          top: 50%
          left: 15px
          display: flex
          justify-content: space-between
          button
            .icon
              font-size: 22px
              line-height: 22px
              transition: color .3s
            &:hover
              .icon
                color: $accent-color
        .owl-dots
          display: flex
          align-items: center
          justify-content: center
          position: absolute
          bottom: 8px
          left: 0
          width: 100%
          button
            display: inline-block
            margin: 0 5px
            span
              display: block
              width: 14px
              height: 14px
              background-color: #999
              border-radius: 50%
              transition: background-color .3s
            &.active
              span
                background-color: $accent-color
  .company-name
    font-family: Montserrat, sans-serif
    @include adaptive-value('font-size', 30, 25, 1)
    @include adaptive-value('line-height', 40, 35, 1)
    font-weight: 400
  .company-descr
    color: #999
    margin: 10px 0 15px
    font-size: 16px
    line-height: 20px
  .company-phone
    display: inline-block
    font-weight: 700
    color: #fff
    font-size: 24px
    line-height: 24px
  .company-menu
    color: #999
    margin: 25px 0
    text-transform: uppercase
    font-weight: 400
    font-size: 18px
    line-height: 20px
    &__list
      margin: 0
      padding: 0
    &__item
      display: block
      margin-bottom: 10px
      &:last-child
        margin-bottom: 0
    &__link
      transition: color .3s
      &:hover
        color: #fff

  &__info
    background-color: #fff
    padding: 15px
    @media (min-width: 960px)
      display: flex
      align-items: stretch
      padding-top: 35px
    @media (min-width: 1024px)
      position: relative
      padding-top: 50px
      &:before
        content: ''
        display: block
        width: 100vw
        height: 100%
        background-color: #fff
        position: absolute
        left: 50%
        right: 50%
        top: 0
        z-index: 0
        transform: translateX(-50%)
    &__left-side
      position: relative
      z-index: 1
      @media (min-width: 960px)
        flex: 0 0 calc(65% - 30px)
        max-width: calc(65% - 30px)
        width: 100%
        margin-right: 30px
        position: relative
        &:after
          content: ''
          display: block
          width: 1px
          height: calc(100% - 50px)
          background-color: #eee
          position: absolute
          top: 50px
          right: -15px
    &__right-side
      position: relative
      z-index: 1
      @media (min-width: 960px)
        flex: 0 0 35%
        max-width: 0 0 35%
        width: 100%
    .info-row
      margin-bottom: 25px
      &__title
        border-bottom: 1px solid #eee
        display: block
        font-weight: 400
        color: #999
        text-transform: uppercase
        font-size: 20px
        line-height: 24px
        padding-bottom: 5px
        margin-bottom: 10px
      &__descr
        font-size: 16px
        line-height: 20px
        display: flex
        flex-direction: column
        max-width: 620px
        .phone-link
          display: inline-block
          font-size: 20px
          line-height: 20px
          transition: color .3s
          margin: 5px 0
          &:hover
            color: $accent-color
        .site-link
          display: inline-block
          font-size: 18px
          line-height: 18px
          transition: color .3s
          margin: 5px 0
          &:hover
            color: $accent-color
        .social-links
          display: flex
          flex-direction: row
          align-items: center
          justify-content: flex-start
          flex-wrap: wrap
          margin: 12px 0
          &__item
            display: inline-block
            margin-right: 10px
          &__link
            display: inline-flex
            align-items: center
            justify-content: center
            border-radius: 50%
            width: 34px
            height: 34px
            font-size: 20px
            line-height: 20px
            color: #fff
            background-color: $txt-color
            transition: background-color .3s
            &:hover
              background-color: $accent-color
        .company-rating
          display: flex
          align-items: center
          justify-content: flex-start
          flex-wrap: wrap
          .rating-list
            display: flex
            color: $accent-color
            .icon
              margin-right: 5px
              position: relative
              top: -1px
          .rating-mark
            font-weight: 800
            display: inline-block
            margin-left: 8px
          .answer-link
            display: inline-block
            margin-left: 15px
        .company-adress
          margin: 10px 0 15px
        .map-block
          height: 280px
          background-color: #eee
          position: relative
          iframe
            position: absolute
            top: 0
            left: 0
            width: 100%
            height: 100%
          @media (min-width: 960px)
            height: 200px
        .map-link
          margin-top: 15px

    .service-row
      margin-bottom: 25px
      &__title
        border-bottom: 1px solid #eee
        display: block
        font-weight: 400
        color: #999
        text-transform: uppercase
        font-size: 20px
        line-height: 24px
        padding-bottom: 5px
        margin-bottom: 10px
      &__item
        margin-bottom: 15px
        &:last-child
          margin-bottom: 0
        .service-name
          display: block
          color: #999
          font-size: 14px
          line-height: 16px
        .service-value
          display: block
          margin-top: 3px
          font-size: 16px
          line-height: 20px

.company-products
  &__title
    flex: 1 1 100%
    border-bottom: 1px solid #eee
    display: block
    font-weight: 400
    color: #999
    text-transform: uppercase
    font-size: 20px
    line-height: 24px
    padding-bottom: 5px
  &__content
    margin-bottom: 15px
    @media (min-width: 1024px)
      display: flex
      flex-wrap: wrap
      align-items: stretch
      margin-bottom: 40px
    &__left-side
      margin: 15px 0
      border-bottom: 1px solid #ccc
      @media (min-width: 1024px)
        width: 100%
        max-width: 230px
        flex: 0 0 230px
        border: none
        border-right: 1px solid #eee
        margin: 0 20px 0 0
      .filter-link
        display: flex
        flex: 1 1 100%
        padding: 10px 0
        font-size: 18px
        line-height: 20px
        font-weight: 600
        @media (min-width: 1024px)
          display: none
        .icon
          margin-right: 10px
          font-size: 24px
          line-height: 20px
          &.icon-filter
            color: rgba($txt-color,.3)
          &.icon-down-open-mini
            margin: 0 0 0 auto
            font-size: 24px
            line-height: 20px
            transform: scale(1,1)
            transition: transform .3s

        &.open
          .icon-down-open-mini
            transform: scale(1,-1)
      .product-filters-block
        border-top: 1px solid #ccc
        @media (min-width: 1024px)
          display: block !important
          border-top: none
          padding: 0 20px 0 0
    &__right-side
      @media (min-width: 1024px)
        flex: 1 1 calc(100% - 250px)
        max-width: calc(100% - 250px)
        .catalog__title
          margin-bottom: 10px
      .category-menu
        margin: 15px 0
        &__list
          position: relative
          padding: 0 20px
          .slide-item
            text-align: center
            .item-icon
              display: flex
              width: 70px
              height: 70px
              align-items: center
              justify-content: center
              margin: 0 auto
            .item-title
              font-size: 14px
              line-height: 14px
              text-align: center
          .owl-nav
            display: flex
            width: 100%
            position: absolute
            top: calc(50% - 12.5px)
            left: 0
            justify-content: space-between
            font-size: 25px
            line-height: 25px
            height: 0px
        &__link
          display: flex
          flex-wrap: wrap
          align-items: center
          justify-content: center
          flex-direction: column
          .item-title
            color: $txt-color
            transition: color .3s
          &:hover
            .item-title
              color: $accent-color
      .catalog__filter
        margin: 30px 0 0
        display: flex
        align-items: center
        justify-content: flex-start
        flex-wrap: wrap
        border-bottom: 1px solid #eee
        padding-bottom: 10px
        @media (min-width: 640px)
          justify-content: space-between
          border-bottom: 1px solid #eee
          padding-bottom: 15px
          margin-top: 20px
        &__sort
          display: flex
          flex: 1 1 auto
          align-items: center
          .icon-signal
            color: rgba($txt-color, .2)
            display: inline-block
            margin-right: 10px
            position: relative
            &:before
              transform: rotate(90deg) scale(-1,1)
          .filter-sort
            margin-right: 40px
            position: relative
            &__link
              color: $accent-color
              display: inline-block
              position: relative
              border-bottom: 1px dotted $accent-color
              font-size: 14px
              line-height: 20px
              @media (min-width: 576px)
                line-height: 14px
              &:after
                position: absolute
                top: 6px
                right: -10px
                content: "\e80a"
                font-family: Fontello
                font-style: normal
                font-weight: 400
                speak: never
                display: inline-block
                margin: 0 auto
                text-decoration: inherit
                text-align: center
                font-variant: normal
                text-transform: none
                display: block
                width: 10px
                height: 10px
            &__list
              position: absolute
              top: calc(100% + 5px)
              left: -10px
              background-color: #fff
              font-size: 14px
              line-height: 20px
              border: 1px solid #ccc
              z-index: 2
              &__item
                border-bottom: 1px solid #ccc
                &:last-child
                  border-bottom: none
              &__type
                display: block
                white-space: nowrap
                padding: 5px 10px
                &:hover
                  color: $accent-color

          .filter-quantity
            position: relative
            @media (min-width: 768px)
              display: block !important
            &__link
              color: $accent-color
              display: inline-block
              position: relative
              border-bottom: 1px dotted $accent-color
              font-size: 14px
              line-height: 20px
              @media (min-width: 576px)
                line-height: 14px
              &:after
                position: absolute
                top: 1px
                right: -15px
                content: "\e80a"
                font-family: Fontello
                font-style: normal
                font-weight: 400
                speak: never
                display: inline-block
                margin: 0 auto
                text-decoration: inherit
                text-align: center
                font-variant: normal
                text-transform: none
                display: block
                width: 10px
                height: 10px
            &__list
              position: absolute
              top: calc(100% + 5px)
              left: -10px
              background-color: #fff
              font-size: 14px
              line-height: 20px
              border: 1px solid #ccc
              z-index: 2
              &__item
                border-bottom: 1px solid #ccc
                &:last-child
                  border-bottom: none
              &__type
                display: block
                white-space: nowrap
                padding: 5px 10px
                &:hover
                  color: $accent-color

          @media (min-width: 576px)
            flex: 1 1 auto
            margin-bottom: 0
        &__view
          display: flex
          align-items: center
          justify-content: flex-end
          flex: 1 1 auto
          .icon
            color: rgba($txt-color, .2)
            transition: color .3s
            font-size: 18px
            line-height: 20px
          .filter-view-type
            display: inline-block
            margin-left: 15px
            &:nth-of-type(1)
              margin-left: 0
            &.active
              .icon
                color: $accent-color
          @media (min-width: 640px)
            flex: 1 1 auto
            justify-content: flex-end
        .filter-name
          display: flex
          align-items: center
          font-size: 14px
          line-height: 14px
          @media (min-width: 576px)
            margin-right: 15px
            span
              display: inline-block !important

      .catalog-list
        margin-top: 15px
        display: flex
        align-items: stretch
        flex-wrap: wrap
        flex: 1 1 100%
        &__see-more
          margin-top: 10px
          @media (min-width: 1024px)
            display: inline-block
            width: auto
            margin: 20px auto 0
            padding: 10px 20px

.company-stocks, .company-news
  margin-bottom: 15px
  @media (min-width: 1024px)
    margin-bottom: 40px
  &__title
    flex: 1 1 100%
    border-bottom: 1px solid #eee
    display: block
    font-weight: 400
    color: #999
    text-transform: uppercase
    font-size: 20px
    line-height: 24px
    padding-bottom: 5px
  &__content
    margin-top: 15px
    position: relative
    .owl-nav
      position: absolute
      display: flex
      align-items: center
      justify-content: space-between
      height: 1px
      width: calc(100% - 20px)
      left: 10px
      top: calc(135px / 2)
      @media (min-width: 375px)
        top: calc(50vw / 2 - 10px)
      @media (min-width: 640px)
        top: 95px
      @media (min-width: 768px)
        top: 85px
      @media (min-width: 960px)
        top: 115px
      @media (min-width: 1024px)
        top: 105px
      button
        .icon
          font-size: 22px
          line-height: 22px
          transition: color .3s
          color: #fff
        &:hover
          .icon
            color: $accent-color
    &__item
      position: relative
      .item-link
        display: block
        width: 100%
        height: 100%
        position: absolute
        top: 0
        left: 0
      .image-block
        .item-image
          display: block
          width: 100%
          max-width: 100%
          margin: 0
      .descr-block
        padding: 10px 0
        .item-title
          font-weight: 700
          font-size: 18px
          line-height: 20px
          transition: color .3s
        .item-content
          margin-top: 10px
          font-size: 15px
          line-height: 20px
          @media (min-width: 576px)
            font-size: 16px
      &:hover
        .descr-block
          .item-title
            color: $accent-color
.company-answers
  &__title
    flex: 1 1 100%
    border-bottom: 1px solid #eee
    display: block
    font-weight: 400
    color: #999
    text-transform: uppercase
    font-size: 20px
    line-height: 24px
    padding-bottom: 5px
  &__content
    margin-top: 15px
    // tmp //
    .product-review-view__posts
      margin-top: 15px
      .product-review-post
        margin-bottom: 25px
      .product-review-post-customer
        display: flex
        justify-content: space-between
        &__address
          display: flex
          flex-wrap: wrap
          align-items: center
          justify-content: flex-start
          font-style: normal
          .review-stars
            display: inline-flex
            align-items: center
            justify-content: flex-start
            margin-right: 10px
            color: #ffcb00
            font-size: 18px
            line-height: 20px
            .icon
              margin-right: 5px
        &__name
          font-size: 18px
          line-height: 18px
          font-weight: 700
        &__date
          font-size: 16px
          line-height: 20px
          color: #999
    .product-review-post__content
      margin-top: 10px
