/* 404 */
.error-page
  background-repeat: no-repeat
  background-position: center
  background-size: cover
  background-color: #4e4846
  position: relative
  text-align: center
  &:before
    content: ''
    display: block
    width: 100%
    height: 100%
    background-color: rgba(0,0,0,.3)
    position: absolute
    top: 0
    left: 0
  &__title
    font-family: Montserrat, sans-serif
    font-weight: 700
    @include adaptive-value('font-size', 142, 50, 1)
    @include adaptive-value('line-height', 142, 50, 1)
  p
    display: inline-block
    margin: 15px 0
    max-width: 245px
    @include adaptive-value('font-size', 22, 16, 1)
    @include adaptive-value('line-height', 22, 20, 1)
    @media (min-width: 414px)
      max-width: 100%
  a
    display: inline-block
    color: #fff
    text-decoration: underline
    transition: color .3s
    &:hover
      color: $accent-color
  &__content
    position: relative
    display: flex
    flex: 1 1 100%
    flex-direction: column
    align-items: center
    justify-content: center
    flex-wrap: nowrap
    z-index: 1
    color: #fff
    @include adaptive-value('padding-bottom', 60, 35, 1)
