/* enterprises page */
.enterprises
  min-height: 100vh
  transition: padding-top .5s
  &__map
    position: relative
    .map-content
      display: block
      position: absolute
      opacity: 0
      top: 0
      left: 0
      width: 100vw
      height: 100vh
      @media (min-width: 1024px)
        display: block
        width: 100%
        height: 100%
        opacity: 1
    .container
      width: auto
  &__content
    position: relative
    z-index: 1
    @media (min-width: 1024px)
      max-width: 600px
      padding: 15px 0
  &__filter
    display: flex
    flex-wrap: wrap
    justify-content: space-between
    background-color: #fff
    margin-bottom: 20px
    @media (min-width: 768px)
      justify-content: flex-start
    @media (min-width: 1024px)
      padding: 15px 15px 0
    &__type
      flex: 1 1 100%
      display: inline-block
      margin: 0 0 10px 0
      background-color: #f9f9f9
      font-size: 15px
      line-height: 20px
      padding: 10px 15px
      text-transform: uppercase
      position: relative
      transition: color .3s
      overflow: hidden
      text-align: center
      &:last-child
        margin-bottom: 0
      @media (min-width: 640px)
        flex: 0 0 auto
        margin: 0
      @media (min-width: 768px)
        margin: 0 15px 15px 0
        &:last-child
          margin-bottom: 15px
      @media (min-width: 1024px)
        margin: 0 10px 15px 0
      @media (min-width: 1200px)
        margin: 0 14px 15px 0
      span
        position: relative
        z-index: 2
      &:before
        content: ''
        display: block
        width: 100%
        height: 100%
        position: absolute
        background-color: $accent-color
        top: 0
        left: 0
        transform: translateY(100%)
        transition: transform .3s
        z-index: 1
      &:hover, &.active
        color: #fff
        &:before
          transform: translateY(0)
      &:last-child
        margin-right: 0
  &__list
    margin: 20px 0
    .slick-arrow
      display: flex
      align-items: center
      justify-content: center
      width: 30px
      height: 30px
      font-size: 30px
      line-height: 30px
      margin: 0 auto
      cursor: pointer
      transition: color .3s
      &:hover
        color: $accent-color
      &:before
        transform: rotate(90deg)
      &.slick-disabled
        opacity: .5
        &:hover
          color: #000
    .slick-list
      margin: 15px 0
      padding-bottom: 20px
    .slick-slide
      margin-bottom: 20px
    &__item
      cursor: pointer
      border: 1px solid #ccc
      margin: 0 auto 15px
      @media (min-width: 1024px)
        border: none
        margin-bottom: 0
      .item-content
        background-color: #fff
        display: flex
        padding: 20px
        position: relative
        flex-direction: column
        @media (min-width: 576px)
          flex-direction: row
          flex-wrap: wrap
        @media (min-width: 1024px)
          &:before
            content: ''
            display: block
            width: 2px
            height: 0
            background-color: $accent-color
            position: absolute
            top: 0
            left: 0
            transition: height .3s
        .item-image
          flex: 0 0 auto
          width: 100%
          max-width: 160px
          @media (min-width: 576px)
            max-width: 190px
          img
            display: block
            width: 100%
            margin: 0
        .item-descr
          flex: 1 1 100%
          width: auto
          max-width: 100%
          padding: 10px 0 0
          @media (min-width: 576px)
            flex: 1 1 auto
            max-width: calc(100% - 190px)
            padding: 0 0 0 20px
          .enterprise-favorite
            display: block
            position: absolute
            top: 10px
            right: 10px
            color: rgba($txt-color, .5)
            transition: color .3s
            &:hover
              color: $accent-color
          .enterprise-name
            font-size: 18px
            line-height: 22px
            font-weight: 600
            transition: color .3s
          .enterprise-caregories
            display: block
            font-size: 15px
            line-height: 20px
            color: #999
            margin-top: 2px
          .enterprise-address
            display: block
            margin-top: 2px
          .enterprise-addinfo
            display: flex
            flex-wrap: wrap
            align-items: center
            margin-top: 10px
            .enterprise-rating
              display: flex
              flex: 0 0 auto
              margin-right: 15px
              .icon
                color: $accent-color
                display: block
                font-size: 16px
                line-height: 16px
                margin-right: 5px
                &:last-child
                  margin-right: 0
            .enterprise-mark
              display: inline-block
              font-weight: 600
              margin-right: 10px
            .enterprise-answers
              .answers-link
                position: relative
                &:after
                  content: ''
                  display: block
                  width: 0
                  height: 1px
                  position: absolute
                  bottom: -1px
                  left: 0
                  transition: width .3s
                  background-color: $accent-color
                &:hover
                  &:after
                    width: 100%
          .enterprise-time
            display: block
            width: 100%
            font-size: 14px
            line-height: 18px
            color: #999
            margin-top: 5px
      &:hover, &.active
        .item-content
          &:before
            height: 100%
          .item-descr
            .enterprise-name
              color: $accent-color
