.lk-company
  background-color: $txt-color
  @media (min-width: 1024px)
    overflow-x: hidden
  &__title
    font-family: Montserrat, sans-serif
    @include adaptive-value('font-size', 30, 25, 1)
    @include adaptive-value('line-height', 40, 35, 1)
    font-weight: 400
    margin-bottom: 25px
    @media (min-width: 1024px)
      border-bottom: 1px solid #333
      padding-bottom: 15px
  &__pagetitle
    font-family: Montserrat, sans-serif
    font-weight: 500
    text-transform: uppercase
    @include adaptive-value('font-size', 28, 20, 1)
    @include adaptive-value('line-height', 32, 24, 1)
    display: block
    margin-bottom: 15px
    @media (min-width: 1024px)
      font-size: 22px
      line-height: 22px
  &__menu
    color: #999
    text-transform: uppercase
    font-weight: 400
    font-size: 18px
    line-height: 20px
    &__list
      margin: 0
      padding: 0
    &__item
      display: block
      margin-bottom: 10px
      @media (min-width: 1024px)
        margin-bottom: 20px
      &:last-child
        margin-bottom: 0
    &__link
      transition: color .3s
      &:hover
        color: #fff
  &__content
    @media (min-width: 960px)
      display: flex
      align-items: stretch
    &__left-side
      padding-bottom: 30px
      color: #fff
      @media (min-width: 960px)
        flex: 0 0 35%
        width: 100%
        max-width: 35%
        padding-right: 15px
      @media (min-width: 1024px)
        padding-top: 15px
    &__right-side
      background-color: #fff
      color: $txt-color
      max-width: calc(100% + 30px)
      width: calc(100% + 30px)
      margin: 0 -15px
      padding: 20px 15px
      @media (min-width: 960px)
        flex: 0 0 calc(65% + 15px)
        width: 100%
        max-width: calc(65% + 15px)
        margin: 0 -15px 0 0
        min-height: calc(100vh - 263px)
      @media (min-width: 1024px)
        padding: 28px 15px 30px 35px
        position: relative
        &:after
          content: ''
          display: block
          width: 100vw
          height: 100%
          position: absolute
          top: 0
          left: -50%
          right: -50%
          background-color: #fff
          transform: translateX(50%)
        & > *
          position: relative
          z-index: 1
      .addstock-link
        @media (min-width: 768px)
          max-width: calc(33.3% - 10px)
  &__info
    .map-block
      height: 280px
      background-color: #eee
      position: relative
      margin-bottom: 20px
      @media (min-width: 1024px)
        max-width: 415px
      iframe
        position: absolute
        top: 0
        left: 0
        width: 100%
        height: 100%
    form
      .input-group
        display: flex
        flex-direction: column-reverse
        align-items: flex-start
        justify-content: flex-start
        margin-bottom: 20px
        label
          font-size: 14px
          line-height: 20px
          color: #999
          display: block
        input
          display: block
          width: 100%
          border: none
          border-bottom: 1px solid #999
          font-size: 16px
          line-height: 20px
          padding: 5px 0
        &.radio-group
          flex: 0 0 100%
          width: auto
          display: flex
          flex-direction: row
          align-items: center
          justify-content: flex-start
          margin: 0 0 10px 0
          @media (min-width: 960px)
            flex: 0 0 auto
            margin: 0 20px 10px 0
          input
            position: absolute
            width: 1px
            height: 1px
            visibility: hidden
            opacity: 0
            &:checked
              + label
                &:before
                  border-color: $accent-color
                &:after
                  opacity: 1
          label
            display: inline-block
            position: relative
            padding-left: 30px
            color: #000
            font-size: 16px
            line-height: 20px
            &:before
              content: ''
              display: block
              width: 20px
              height: 20px
              border: 1px solid #000
              position: absolute
              left: 0
              top: 0
              border-radius: 50%
              transition: border-color .3s
            &:after
              content: ''
              display: block
              width: 12px
              height: 12px
              background-color: $accent-color
              border-radius: 50%
              position: absolute
              left: 4px
              top: 4px
              opacity: 0
              transition: opacity .3s
        &.select-group
          width: 100%
        &.checkbox-group
          width: 100%
          flex: 0 0 100%
          display: flex
          input
            position: absolute
            width: 0px
            height: 0px
            visibility: hidden
            opacity: 0
            &:checked
              & + label
                &:before
                  border-color: $accent-color
                  background-color: $accent-color
                &:after
                  opacity: 1
          label
            display: inline-block
            font-size: 16px
            line-height: 18px
            color: #000
            padding-left: 30px
            position: relative
            &:before
              content: ''
              display: block
              width: 20px
              height: 20px
              border: 1px solid #999
              border-radius: 3px
              position: absolute
              top: 0
              left: 0
            &:after
              display: inline-block
              position: absolute
              left: 3px
              top: 3px
              margin: 0 auto
              text-align: center
              content: "\e809"
              font-family: Fontello
              width: 14px
              height: 14px
              position: absolute
              font-size: 14px
              line-height: 14px
              color: #fff
              opacity: 0
              transition: opacity .3s
        &.btn-group
          display: flex
          align-items: center
          flex-direction: row
          justify-content: flex-start
          @media (min-width: 960px)
            margin-top: 30px
            margin-bottom: 0
          button
            margin-right: 21px
          .btn-border-style
            color: $txt-color
            flex: 0 0 auto
            display: inline-flex
            align-items: center
            justify-content: center
            width: auto
            padding: 10px 26px
            &:hover
              color: #fff
      .input-row
        display: flex
        flex-wrap: wrap
        .input-title
          display: block
          width: 100%
          flex: 1 1 100%
          margin-bottom: 15px
      .input-content
        display: flex
        flex-wrap: wrap
        margin: 20px 0
        .input-group
          &.select-group
            flex: 0 0 100%
            width: 100%
            select
              width: 100%
              border: 1px solid #999
              padding: 5px
              @media (min-width: 576px)
                max-width: 320px
          &.time-group
            display: flex
            flex: 0 0 100%
            flex-direction: row
            align-items: stretch
            margin-bottom: 0
            @media (min-width: 576px)
              max-width: 320px
            input
              flex: 1 0 auto
              align-items: center
              justify-content: flex-start
              width: auto
              border: 1px solid #999
              padding: 5px 10px
            span
              display: inline-flex
              align-items: center
              justify-content: center
              flex: 0 0 30px
              margin: 0 auto
            .addbtn
              display: inline-flex
              align-items: center
              justify-content: center
              flex: 0 0 35px
              margin: 0 0 0 15px
              border: 1px solid #999
  &__stocks
    margin-top: 20px
    display: flex
    flex-wrap: wrap
    &__item
      flex: 0 0 calc(50% - 10px)
      max-width: calc(50% - 10px)
      width: 100%
      margin: 0 20px 20px 0
      &:nth-of-type(2n)
        margin-right: 0
      @media (min-width: 576px)
        flex: 0 0 calc(33.3% - 10px)
        max-width: calc(33.3% - 10px)
        width: 100%
        margin: 0 15px 15px 0
        &:nth-of-type(2n)
          margin-right: 15px
        &:nth-of-type(3n)
          margin-right: 0
      @media (min-width: 768px)
        border: 1px solid #999
      @media (min-width: 1025px)
        transition: border-color .3s
        position: relative
        &:after
          content: ''
          display: block
          width: 100%
          height: 100%
          background-color: rgba(0,0,0,.8)
          position: absolute
          top: 0
          left: 0
          opacity: 0
          transition: opacity .3s
        &:hover
          border-color: #000
          .btns-block
            opacity: 1
          &:after
            opacity: 1
      .image-block
        .stock-image
          display: block
          width: 100%
          max-width: 100%
          margin: 0
      .descr-block
        padding: 10px 0
        @media (min-width: 768px)
          padding: 10px
        @media (min-width: 1025px)
          padding: 20px 15px
        .stock-name
          font-weight: 700
          font-size: 18px
          line-height: 20px
          transition: color .3s
        .stock-descr
          margin-top: 10px
          font-size: 15px
          line-height: 20px
          @media (min-width: 576px)
            font-size: 16px
      .btns-block
        margin-top: 10px
        @media (min-width: 768px)
          padding: 0 10px
        @media (min-width: 1025px)
          position: absolute
          display: flex
          align-items: center
          justify-content: center
          flex-direction: column
          opacity: 0
          z-index: 10
          top: 0
          left: 0
          width: 100%
          height: 100%
          transition: opacity .3s
        .stock-edit-links
          font-size: 13px
          line-height: 20px
          padding: 8px
          margin: 0 0 10px 0
          .icon
            display: inline-block
            margin-right: 7px
          @media (min-width: 1025px)
            border-color: #fff
            color: #fff

  &__edit
    margin-bottom: 30px
    form
      .input-title
        font-size: 14px
        line-height: 20px
        color: #999
        display: block
        margin-bottom: 10px
      .input-group
        display: flex
        flex-direction: column-reverse
        align-items: flex-start
        justify-content: flex-start
        margin-bottom: 20px
        label
          font-size: 14px
          line-height: 20px
          color: #999
          display: block
        input, textarea
          display: block
          width: 100%
          max-width: 100%
          border: none
          border-bottom: 1px solid #999
          font-size: 16px
          line-height: 20px
          padding: 5px 0
        textarea
          height: 32px

        &.radio-group
          flex: 0 0 100%
          width: auto
          display: flex
          flex-direction: row
          align-items: center
          justify-content: flex-start
          margin: 0 0 10px 0
          @media (min-width: 960px)
            flex: 0 0 auto
            margin: 0 20px 10px 0
          input
            position: absolute
            width: 1px
            height: 1px
            visibility: hidden
            opacity: 0
            &:checked
              + label
                &:before
                  border-color: $accent-color
                &:after
                  opacity: 1
          label
            display: inline-block
            position: relative
            padding-left: 30px
            color: #000
            font-size: 16px
            line-height: 20px
            &:before
              content: ''
              display: block
              width: 20px
              height: 20px
              border: 1px solid #000
              position: absolute
              left: 0
              top: 0
              border-radius: 50%
              transition: border-color .3s
            &:after
              content: ''
              display: block
              width: 12px
              height: 12px
              background-color: $accent-color
              border-radius: 50%
              position: absolute
              left: 4px
              top: 4px
              opacity: 0
              transition: opacity .3s
        &.select-group
          width: 100%
        &.image-group
          flex-direction: row
          align-items: stretch
          justify-content: flex-start
          flex-wrap: wrap
          width: 100%
          max-width: 100%
          flex: 1 1 100%
          .input-title
            flex: 1 1 100%
            width: 100%
          .pip
            display: flex
            align-items: center
            justify-content: center
            flex: 0 0 60px
            max-width: 60px
            height: 60px
            border: 1px solid #999
            position: relative
            margin-bottom: 10px
            margin-right: 10px
            img
              display: block
              width: auto
              max-width: 100%
              margin: 0 auto
              max-height: 100%
            .remove
              position: absolute
              top: 3px
              right: 5px
              font-size: 14px
              line-height: 14px
              cursor: pointer
              color: $accent-color

          label
            position: relative
            flex: 0 0 60px
            width: 60px
            height: 60px
            display: flex
            align-items: center
            justify-content: center
            background-color: #999
            border: 1px solid transparent
            transition: border-color .3s
            margin-right: 10px
            margin-bottom: 10px
            &:hover
              border-color: $accent-color
            &:after
              content: '\e807'
              font-family: Fontello
              font-style: normal
              font-weight: normal
              speak: never
              display: inline-block
              margin: 0 auto
              text-decoration: inherit
              text-align: center
              font-variant: normal
              text-transform: none
              z-index: 1
              color: #fff
              font-size: 24px
              line-height: 24px
          input
            width: 0
            height: 0
            position: absolute
            opacity: 0

        &.search-group
          position: relative
          max-width: 500px
          &:after
            font-family: Fontello
            font-style: normal
            font-weight: normal
            speak: never
            display: inline-block
            margin: 0 auto
            text-decoration: inherit
            text-align: center
            font-variant: normal
            text-transform: none
            content: '\e80e'
            position: absolute
            bottom: 0
            right: 3px
            font-size: 24px
            line-height: 24px
        &.serv-types
          flex-direction: column
          .serv-type-title
            font-size: 20px
            line-height: 20px
            display: block
            width: 100%
            margin-bottom: 10px
          .serv-types__item
            display: flex
            flex-wrap: wrap
            .serv-type
              font-size: 16px
              line-height: 20px
              background-color: #f9f9f9
              display: inline-block
              margin: 0 15px 10px 0
              padding: 5px 10px
              .icon
                display: inline-block
                margin-left: 5px
                font-size: 14px
                line-height: 14px
                cursor: pointer
        &.checkbox-group
          width: 100%
          flex: 0 0 100%
          display: flex
          input
            position: absolute
            width: 0px
            height: 0px
            visibility: hidden
            opacity: 0
            &:checked
              & + label
                &:before
                  border-color: $accent-color
                  background-color: $accent-color
                &:after
                  opacity: 1
          label
            display: inline-block
            font-size: 16px
            line-height: 18px
            color: #000
            padding-left: 30px
            position: relative
            &:before
              content: ''
              display: block
              width: 20px
              height: 20px
              border: 1px solid #999
              border-radius: 3px
              position: absolute
              top: 0
              left: 0
            &:after
              display: inline-block
              position: absolute
              left: 3px
              top: 3px
              margin: 0 auto
              text-align: center
              content: "\e809"
              font-family: Fontello
              width: 14px
              height: 14px
              position: absolute
              font-size: 14px
              line-height: 14px
              color: #fff
              opacity: 0
              transition: opacity .3s
        &.btn-group
          display: flex
          align-items: center
          flex-direction: row
          justify-content: flex-start
          @media (min-width: 960px)
            margin-top: 30px
            margin-bottom: 0
          button
            margin-right: 21px
          .btn-border-style
            color: $txt-color
            flex: 0 0 auto
            display: inline-flex
            align-items: center
            justify-content: center
            width: auto
            padding: 10px 26px
            &:hover
              color: #fff
      .input-row
        display: flex
        flex-wrap: wrap
        .input-title
          display: block
          width: 100%
          flex: 1 1 100%
          margin-bottom: 15px
      .input-content
        display: flex
        flex-wrap: wrap
        margin: 20px 0
        .input-group
          &.select-group
            flex: 0 0 100%
            width: 100%
            select
              width: 100%
              border: 1px solid #999
              padding: 5px
              @media (min-width: 576px)
                max-width: 320px
          &.time-group
            display: flex
            flex: 0 0 100%
            flex-direction: row
            align-items: stretch
            margin-bottom: 0
            flex-wrap: wrap
            @media (min-width: 360px)
              flex-wrap: nowrap
            @media (min-width: 576px)
              max-width: 320px
            input
              flex: 0 1 auto
              align-items: center
              justify-content: flex-start
              width: auto
              border: 1px solid #999
              padding: 5px 10px
              @media (min-width: 360px)
                max-width: calc(45% - 5px)
              @media (min-width: 576px)
                max-width: 50%
              &:nth-of-type(1)
                margin-bottom: 10px
                @media (min-width: 360px)
                  margin-bottom: 0
                  margin-right: 10px
                @media (min-width: 375px)
                  margin-right: 15px
                @media (min-width: 414px)
                  margin-right: 0
            span
              align-items: center
              justify-content: center
              flex: 0 0 25px
              margin: 0 auto
              @media (min-width: 414px)
                display: inline-flex !important
            .addbtn
              display: inline-flex
              align-items: center
              justify-content: center
              flex: 0 0 35px
              margin: 0 0 0 15px
              border: 1px solid #999









