.cart-main
  padding-top: 30px
  .back-link
    display: inline-flex
    align-items: center
    color: $accent-color
    margin-bottom: 10px
    .icon
      display: inline-block
      margin-right: 5px
      transition: transform .3s
    &:hover
      .icon
        transform: translateX(-5px)
  &__title
    font-weight: 700
    span
      display: inline-block
      font-size: 14px
      line-height: 14px
      font-weight: 400
      position: relative
      bottom: 8px
  &__content
    margin: 20px 0 40px
    @media (min-width: 960px)
      display: flex
      align-items: flex-start
    &__left-side
      @media (min-width: 960px)
        flex: 1 1 auto
        width: calc(100% - 20px)
        margin-right: 20px
      .delivery-type
        margin-bottom: 30px
        &__title
          font-weight: 400
          padding-bottom: 8px
          margin-bottom: 15px
        &__menu
          display: flex
          flex-wrap: wrap
          .input-group
            flex: 1 1 100%
            width: 100%
            border: 1px solid #999
            margin-bottom: 15px
            transition: border-color .3s
            @media (min-width: 576px)
              flex: 0 0 auto
              width: auto
              margin: 0 15px 15px 0
              min-width: calc(33.3% - 10px)
              &:nth-of-type(3n)
                margin-right: 0
            &.active
              border-color: $accent-color
            input
              position: absolute
              width: 0
              height: 0
              visibility: hidden
              opacity: 0
            label
              padding: 15px
              display: block
              outline: none
              .type-name
                font-size: 16px
                line-height: 20px
              .type-price
                color: #999
                font-size: 16px
                line-height: 20px
            span
              display: block
              &:last-child
                margin-bottom: 0
        &__content
          border-top: 1px solid #000
          padding-top: 15px
          margin-bottom: 15px
          @media (min-width: 960px)
            max-width: 380px
            border-top: none
          .delivery_adress
            border: none
            color: #000
            width: 100%
          .select-adress
            display: inline-block
            color: $accent-color
            margin-top: 10px
          &__tab
            .input-group
              margin-bottom: 15px
              &:last-child
                margin-bottom: 0
              input, textarea
                border: none
                border-bottom: 1px solid #999
                width: 100%
                padding: 5px 0
            .input-row
              @media (min-width: 960px)
                display: flex
              .input-group
                flex: 1 1 100%
                width: 100%
                @media (min-width: 960px)
                  flex: 0 0 calc(25% - 10px)
                  max-width: calc(25% - 10px)
                  width: auto
                  margin: 0 13px 13px 0
                  &:nth-of-type(4n)
                    margin-right: 0
                  input
                    width: auto
                    max-width: 100%

      .user-info
        margin-bottom: 30px
        &__content
          padding-top: 10px
          @media (min-width: 960px)
            display: flex
            align-items: flex-start
          &__left-side
            margin-bottom: 15px
            @media (min-width: 960px)
              flex: 0 0 380px
              width: 100%
              max-width: 380px
          &__right-side
            flex: 0 0 auto
            width: auto
            margin-left: auto
            .login-link
              @media (min-width: 576px)
                max-width: 280px
              @media (min-width: 960px)
                padding: 10px 40px
                margin-top: 5px
          .input-group
            margin-bottom: 15px
            &:last-child
              margin-bottom: 0
            input, textarea
              border: none
              border-bottom: 1px solid #999
              width: 100%
              padding: 5px 0
      .payment-type
        &__content
          display: flex
          flex-wrap: wrap
          padding-top: 20px
          &__item
            flex: 1 1 100%
            width: 100%
            @media (min-width: 576px)
              flex: 0 0 auto
              width: auto
              margin-right: 15px
          .input-group
            border: 1px solid #999
            margin-bottom: 15px
            transition: border-color .3s
            &.active
              border-color: $accent-color
            input
              position: absolute
              width: 0
              height: 0
              visibility: hidden
              opacity: 0
            label
              padding: 15px
              display: block
              outline: none
              .type-name
                font-size: 16px
                line-height: 20px
              .type-price
                color: #999
                font-size: 16px
                line-height: 20px
            span
              display: block
              &:last-child
                margin-bottom: 0

    &__right-side
      @media (min-width: 960px)
        flex: 0 0 300px
        max-width: 300px
        width: 100%
    .allcheck
      display: inline-flex
      align-items: flex-start
      position: relative
      font-size: 16px
      line-height: 18px
      &:before
        content: ''
        display: inline-block
        width: 18px
        height: 18px
        border: 1px solid $txt-color
        margin-right: 10px
        transition: background-color .3s, border-color .3s
      &:after
        font-family: Fontello
        font-style: normal
        font-weight: normal
        speak: never
        display: inline-block
        margin: 0 auto
        text-decoration: inherit
        text-align: center
        font-variant: normal
        text-transform: none
        content: '\e809'
        width: 14px
        height: 14px
        position: absolute
        left: 2px
        top: 2px
        font-size: 14px
        line-height: 14px
        color: #fff
        opacity: 0
        transition: opacity .3s
      &.active
        &:before
          background-color: $accent-color
          border-color: $accent-color
        &:after
          opacity: 1
    .cart-list
      border-top: 1px solid #e6e6e6
      margin-top: 10px
      padding-top: 10px
      &__item
        display: flex
        flex-wrap: wrap
        margin-bottom: 20px
        padding-bottom: 20px
        border-bottom: 1px solid #e6e6e6
        @media (min-width: 640px)
          flex-wrap: nowrap
          align-items: flex-start
        &:last-child
          margin-bottom: 0
          border-bottom: none
        .item-check
          display: flex
          flex: 0 0 20px
          margin-right: 10px
          label
            outline: none
            display: block
            width: 20px
            height: 20px
          input
            position: absolute
            visibility: hidden
            height: 0px
            width: 0px
            opacity: 0
            &:checked + span
              &:before
                background-color: $accent-color
                border-color: $accent-color
              &:after
                opacity: 1
          span
            display: block
            width: 20px
            height: 20px
            position: relative
            &:before
              content: ''
              display: inline-block
              width: 18px
              height: 18px
              border: 1px solid $txt-color
              margin-right: 10px
              transition: background-color .3s, border-color .3s
            &:after
              font-family: Fontello
              font-style: normal
              font-weight: normal
              speak: never
              display: inline-block
              margin: 0 auto
              text-decoration: inherit
              text-align: center
              font-variant: normal
              text-transform: none
              content: '\e809'
              width: 14px
              height: 14px
              position: absolute
              left: 2px
              top: 2px
              font-size: 14px
              line-height: 14px
              color: #fff
              opacity: 0
              transition: opacity .3s
        .item-image
          flex: 0 0 auto
          width: auto
          max-width: 100%
          @media (min-width: 576px)
            max-width: 100px
            flex: 0 0 100px
        .item-descr
          flex: 1 1 100%
          max-width: 100%
          width: 100%
          padding-left: 30px
          padding-top: 10px
          @media (min-width: 576px)
            flex: 1 1 auto
            width: auto
            padding-top: 0
            padding-left: 20px
          @media (min-width: 640px)
            padding-right: 20px
          .item-title
            font-size: 18px
            line-height: 20px
            font-weight: 600
          .item-properties
            color: #999
            font-size: 15px
            line-height: 20px
            margin: 5px 0
            &__item
              display: block
              width: 100%
              margin-bottom: 5px
              &:last-child
                margin-bottom: 0
          .item-enterprise
            color: $accent-color
            font-size: 15px
            line-height: 20px
            display: inline-block
            margin-bottom: 5px
          .item-btns
            display: flex
            margin-top: 10px
            a
              display: inline-flex
              align-items: center
              justify-content: flex-start
              flex: 0 0 auto
              color: $accent-color
              font-size: 15px
              line-height: 15px
              transition: color .3s
              &:hover
                color: #000
              .icon
                font-size: 14px
                line-height: 15px
                display: inline-block
                margin-right: 8px
              &:nth-of-type(1)
                margin-right: 25px
        .item-info
          display: flex
          align-items: center
          justify-content: flex-start
          padding-left: 30px
          margin-top: 15px
          @media (min-width: 576px)
            padding-left: 150px
            margin-top: 20px
          @media (min-width: 640px)
            padding-left: 0
            margin-top: 0
          .input-group.count-group
            display: flex
            align-items: center
            justify-content: center
            flex: 0 0 auto
            width: auto
            input
              border: none
              text-align: center
              flex: 0 0 35px
              width: auto
              max-width: 35px
            .count-btn
              border: 1px solid #000
              display: inline-flex
              align-items: center
              justify-content: center
              flex: 0 0 30px
              width: 30px
              height: 30px
              transition: color .3s, border-color .3s
              &:hover
                color: $accent-color
                border-color: $accent-color
          .item-price
            display: inline-block
            font-weight: 700
            font-size: 20px
            line-height: 20px
            margin-left: 20px

    .result-list
      border: 1px solid #e6e6e6
      padding: 15px
      margin-top: 30px
      &__all
        display: flex
        align-items: center
        justify-content: space-between
        flex-wrap: wrap
        font-weight: 700
        border-bottom: 1px solid #e6e6e6
        font-size: 20px
        line-height: 20px
        padding-bottom: 5px
        max-width: 420px
        margin: 0 auto 15px
      &__products, &__stock
        display: flex
        align-items: center
        justify-content: space-between
        font-size: 16px
        line-height: 20px
        margin: 0 auto 10px
        max-width: 420px
        &:last-child
          margin-bottom: 0
        .value
          font-weight: 700
      &__promo
        margin: 30px auto 0
        max-width: 420px
        .input-group
          display: flex
          align-items: stretch
          input
            border: 1px solid #b3b3b3
            border-right: none
            flex: 1 1 100%
            max-width: 100%
            width: 100%
            padding: 10px
            font-size: 14px
            line-height: 14px
          button
            background-color: #b3b3b3
            flex: 0 0 auto
            width: auto
            padding: 0 10px
            border: none
            color: #fff
            font-size: 14px
            line-height: 14px
            text-transform: uppercase
            font-weight: 100
            transition: background-color .3s
            &:hover
              background-color: $accent-color
      &__submit
        max-width: 420px
        margin: 15px auto 0
        button
          padding: 10px 0
          background-color: $accent-color
          text-transform: uppercase
          color: #fff
          font-weight: 600
          border: 1px solid $accent-color
          display: block
          width: 100%
          transition: background-color .3s, border-color .3s
          &:hover
            background-color: #b5262e
            border-color: #b5262e
      &__check
        font-size: 14px
        line-height: 20px
        margin-top: 10px
        a
          display: inline-block
          color: $accent-color
          text-decoration: underline
          &:hover
            text-decoration: none


