/* stocks page */
.stocks-info
  margin-top: 10px
  overflow-x: hidden
  @media (min-width: 768px)
    margin-top: 0
  &__row
    display: flex
    flex-wrap: wrap
    flex-direction: row-reverse
    @media (min-width: 960px)
      flex-wrap: nowrap
      align-items: stretch
      &:nth-of-type(2n)
        flex-direction: row
    &__left-side
      order: 2
      display: flex
      flex-wrap: wrap
      align-items: center
      flex: 1 1 100%
      background-color: #f6f6f6
      padding: 20px
      min-height: 100vw
      .descr-block
        max-width: 300px
        margin: 0 auto
        @media (min-width: 414px)
          max-width: 320px
        @media (min-width: 576px)
          max-width: 340px
        @media (min-width: 640px)
          max-width: 355px
        @media (min-width: 960px)
          max-width: 400px
        @media (min-width: 1200px)
          max-width: 425px
      @media (min-width: 768px)
        min-height: 50vw
        padding: 30px
      @media (min-width: 960px)
        order: 1
        flex: 1 1 50%
        min-height: auto
      @media (min-width: 1024px)
        position: relative
        &:before
          content: ''
          display: block
          width: 100vw
          height: 100%
          position: absolute
          right: 0
          top: 0
          z-index: -1
          background-color: #f6f6f6
      a
        display: inline-block
    &__right-side
      order: 1
      flex: 1 1 100%
      @media (min-width: 960px)
        flex: 1 1 50%
        order: 2
      img
        display: block
        width: 100%
        max-width: 100%
        margin: 0 auto
    &:nth-of-type(2n)
      .stocks-info__row__left-side
        background-color: transparent
        &:before
          display: none
    &:last-child
      .about-block__row__left-side
        margin-bottom: 0
  &__descr
    font-size: 15px
    line-height: 22px
    @media (min-width: 414px)
      font-size: 16px
    @media (min-width: 768px)
      font-size: 15px
      line-height: 20px
    @media (min-width: 960px)
      font-size: 18px
      line-height: 24px
    @media (min-width: 1200px)
      font-size: 19px
      line-height: 27px
    p
      margin-bottom: 10px
      @media (min-width: 960px)
        margin-bottom: 20px
      span
        display: inline-block
        font-size: 350%
        line-height: 100%
        float: left
        margin: -6px 3px -5px -5px
        text-align: left
        @media (min-width: 414px)
          margin: -8px 3px -5px -5px
        @media (min-width: 960px)
          font-size: 450%
          margin: -26px 5px -10px -8px
      &:last-child
        margin-bottom: 0
    a
      color: $txt-color
      text-decoration: none
      transition: color .3s
      &:hover
        color: $accent-color
.enterprises-stocks
  @include adaptive-value('margin-top', 80, 20, 1)
  &__title
    font-family: Montserrat, sans-serif
    font-weight: 600
    text-align: center
    @media (min-width: 1200px)
      font-size: 38px
  &__content
    @include adaptive-value('margin-top', 40, 20, 1)
    @media (min-width: 414px)
      display: flex
      flex-wrap: wrap
      justify-content: center
      margin-bottom: 15px
    &__item
      max-width: 100%
      margin: 0 auto 15px
      overflow: hidden
      position: relative
      @media (min-width: 340px)
        max-width: 300px
        margin: 0 auto 30px
      @media (min-width: 414px)
        flex: 1 1 calc(50% - 5px)
        max-width: calc(50% - 5px)
        margin: 0 auto 5px
      @media (min-width: 576px)
        flex: 1 1 calc(50% - 10px)
        max-width: calc(50% - 10px)
        margin: 0 auto 10px
      @media (min-width: 768px)
        flex: 1 1 calc(33% - 10px)
        max-width: calc(33% - 10px)
        margin: 0 auto 10px
      @media (min-width: 1024px)
        flex: 1 1 calc(25% - 15px)
        max-width: calc(25% - 15px)
        margin: 0 auto 15px
      .enterprise-link
        display: block
        width: 100%
        height: 100%
        position: absolute
        top: 0
        left: 0
        z-index: 5
        outline: none
      .image-block
        position: relative
        img
          display: block
          width: 100%
          max-width: 100%
          margin: 0
        .enterprise-name
          display: flex
          align-items: center
          justify-content: center
          padding: 20px
          width: 100%
          height: 100%
          position: absolute
          z-index: 1
          top: 0
          left: 0
          color: #fff
          background-color: rgba($txt-color, .8)
          text-align: center
          font-size: 18px
          line-height: 22px
          font-weight: 600
      .descr-block
        display: flex
        align-items: center
        justify-content: center
        text-align: center
        width: 100%
        height: 100%
        transform: translateY(100%)
        transition: transform .3s
        position: absolute
        top: 0
        left: 0
        color: #fff
        background-color: $accent-color
        padding: 15px
        z-index: 2
        outline: none
        font-weight: 600
        @media (min-width: 414px)
          font-size: 14px
        @media (min-width: 576px)
          font-size: 16px
        p
          margin-bottom: 10px
          &:last-child
            margin-bottom: 0
      &:hover
        .descr-block
          transform: translateY(0%)
