.partner_form
  max-width: 600px
  margin: 0 auto
  padding: 0 10px
  .modal-body
    padding: 50px 0
  .modal-title
    color: #fff
    font-size: 20px
    line-height: 26px
    margin-bottom: 15px
    span
      display: block
  .modal-form
    color: #fff
    .input-group
      margin-bottom: 25px
      &:last-child
        margin-bottom: 0
      &.agree-group
        font-size: 14px
        line-height: 20px
        a
          color: $accent-color
          text-decoration: underline
          display: inline-block
          transition: color .3s
          &:hover
            color: #fff

.login_form
  max-width: 420px
  margin: 0 auto
  @media (min-width: 1024px)
    max-width: 600px
  .modal-body
    padding: 50px 0
  .modal-title
    border-bottom: 2px solid #fff
    color: #fff
    font-size: 24px
    line-height: 24px
    font-weight: 600
    padding-bottom: 5px
  .modal-form
    color: #fff
    .input-group
      margin-bottom: 25px
      &:last-child
        margin-bottom: 0
      &.check-group
        input
          width: 0
          height: 0
          position: absolute
          visibility: hidden
          opacity: 0
          &:checked + label
            &:before
              background-color: $accent-color
              border-color: $accent-color
            &:after
              opacity: 1
        label
          display: inline-flex
          align-items: center
          justify-content: flex-start
          position: relative
          line-height: 16px
          &:before
            content: ''
            display: inline-block
            width: 18px
            height: 18px
            border: 1px solid #fff
            margin-right: 10px
            transition: background-color .3s, border-color .3s
          &:after
            font-family: Fontello
            font-style: normal
            font-weight: normal
            speak: never
            display: inline-block
            margin: 0 auto
            text-decoration: inherit
            text-align: center
            font-variant: normal
            text-transform: none
            content: '\e809'
            width: 14px
            height: 14px
            position: absolute
            left: 2px
            top: 2px
            font-size: 14px
            line-height: 14px
            color: #fff
            opacity: 0
            transition: opacity .3s
      &.agree-group
        font-size: 14px
        line-height: 20px
        text-align: center
        a
          color: $accent-color
          text-decoration: underline
          display: inline-block
          transition: color .3s
          &:hover
            color: #fff
      &.social-group
        text-align: center
        .social-links
          display: flex
          align-items: stretch
          justify-content: center
          flex-wrap: wrap
          margin-top: 10px
          &__item
            margin: 0 5px
          &__link
            display: inline-flex
            align-items: center
            justify-content: center
            background-color: #fff
            border-radius: 50%
            flex: 0 0 35px
            width: 35px
            height: 35px
            color: #000
            transition: background-color .3s, color .3s
            font-size: 20px
            line-height: 20px
            &:hover
              background-color: $accent-color
              color: #fff
      &.change-form
        text-align: center
        a
          color: $accent-color
          transition: color .3s
          &:hover
            color: #fff
          &.register
            text-transform: uppercase
      &.btn-group
        @media (min-width: 1024px)
          max-width: 250px
          margin: 0 auto



button.mfp-close
  font-size: 45px
  line-height: 55px
  width: 55px
  height: 55px
  opacity: 1!important

