.addproducts
  margin-bottom: 30px
  @media (min-width: 1200px)
    margin-bottom: 40px
  &__title
    text-align: center
    @media (min-width: 576px)
      text-align: left
  &__content
    margin-top: 20px
    padding: 0 20px
    position: relative
    @media (min-width: 1200px)
      padding: 0
    .owl-nav
      position: absolute
      display: flex
      justify-content: space-between
      width: 100%
      height: 1px
      left: 0
      top: 33%
      font-size: 20px
      line-height: 20px
      @media (min-width: 1200px)
        top: 80px
        padding: 0 15px
        color: #fff
        font-size: 35px
    .owl-stage
      display: flex
      align-items: stretch
      padding: 0 2px
    .catalog-list__item
      flex: 1 1 calc(100% - 2px)
      max-width: calc(100% - 2px)
      width: 100%
      margin: 0 auto
      display: flex
      flex-direction: column
      align-items: stretch
      justify-content: stretch
      height: 100%
      &:nth-of-type(2n)
        margin-right: 0
      .image-block
        position: relative
        .item-image
          display: block
          width: 100%
          max-width: 100%
          margin: 0
        .stickers
          display: flex
          flex-wrap: wrap
          flex: 1 1 calc(100% - 20px)
          max-width: calc(100% - 20px)
          position: absolute
          top: 10px
          left: 10px
          z-index: 1
          @media (min-width: 1025px)
            flex: 1 1 calc(100% - 50px)
            max-width: calc(100% - 50px)
          &__item
            margin: 0 0 6px 6px
            border-radius: 30px
            color: #fff
            padding: 5px 10px
            font-size: 12px
            line-height: 14px
            font-weight: 600
            @media (min-width: 360px)
              font-size: 13px
            &:nth-of-type(1)
              margin-left: 0
        .item-link
          display: block
          position: relative
          &:after
            content: ''
            display: block
            width: 100%
            height: 100%
            position: absolute
            top: 0
            left: 0
            border: 1px solid transparent
            border-bottom: none
            transition: border-color .3s
        @media (min-width: 1025px)
          .additem-btns
            display: block !important
            overflow: hidden
            position: absolute
            top: 10px
            right: 10px
            z-index: 1
            max-height: 0
            transition: max-height .5s
            &__list
              display: flex
              flex-direction: column
              align-items: center
              justify-content: center
              background-color: #fff
              border: 1px solid #ccc
              padding: 5px
              border-radius: 3px
            &__link
              display: block
              font-size: 18px
              line-height: 18px
              color: rgba($txt-color, .5)
              margin-bottom: 10px
              transition: color .3s
              &.active
                color: $accent-color
              &:hover
                color: $accent-color
              &:last-child
                margin-bottom: 0
      .descr-block
        display: flex
        flex-direction: column
        flex: 1 1 100%
        align-items: flex-start
        justify-content: space-between
        padding: 10px
        border: 1px solid #ccc
        border-top: none
        transition: border-color .3s
        position: relative
        .item-info
          display: flex
          width: 100%
          flex-wrap: wrap
          .item-marks
            display: flex
            align-items: center
            .item-rating
              color: $accent-color
              font-size: 14px
              line-height: 14px
              margin-bottom: -2px
              @media (min-width: 576px)
                font-size: 16px
                line-height: 16px
            .item-answers
              color: rgba($txt-color, .5)
              display: inline-block
              margin-left: 8px
              line-height: 12px
              font-size: 14px
              @media (min-width: 576px)
                font-size: 16px
                line-height: 16px
          .item-name
            display: inline-block
            font-weight: 600
            font-size: 18px
            line-height: 20px
            margin: 10px 0
            transition: color .3s
          .item-price
            display: flex
            flex-wrap: wrap
            flex: 1 1 100%
            span
              flex: 0 1 auto
              font-size: 22px
              line-height: 22px
              font-weight: 700
              &:nth-of-type(1)
                margin-right: 10px
              &.item-oldprice
                text-decoration: line-through
                color: rgba($txt-color, .5)
              &.item-price
                color: $txt-color
              &.item-oldprice + .item-price
                color: $accent-color
          .item-available
            flex: 1 1 100%
            margin: 10px 0
            @media (min-width: 1025px)
              display: flex !important
            span
              display: flex
              align-items: center
              justify-content: flex-start
              font-size: 16px
              line-height: 16px
              .icon
                margin-right: 5px
                margin-bottom: -1px
              &.ok
                color: green
              &.not
                color: rgba($txt-color, .5)
        .addcart-form
          display: flex
          flex: 1 1 100%
          width: 100%
          align-items: flex-end
          margin-top: 10px
          .input-group
            flex: 1 0 auto
            &.count-group
              display: flex
              align-items: stretch
              flex: 0 0 auto
              margin-right: 10px
              .count-input
                display: block
                flex: 1 1 auto
                width: 46px
                max-width: 46px
                text-align: center
                border: 1px solid rgba($txt-color, .3)
              .btns
                display: flex
                flex-direction: column
                .count-btn
                  display: flex
                  align-items: center
                  justify-content: center
                  border: 1px solid rgba($txt-color, .3)
                  border-left: none
                  flex: 1 1 100%
                  padding: 1px 6px
                  color: $txt-color
                  transition: color .3s
                  &:hover
                    color: $accent-color
                  &:nth-of-type(1)
                    border-bottom: none
            &.btn-group
              flex: 1 1 100%
              button
                margin-bottom: 0
                padding: 12px
      &:hover
        .image-block
          .additem-btns
            max-height: 100px
          .item-link
            &:after
              border-color: $accent-color
        .descr-block
          border-color: $accent-color
          .item-info
            .item-name
              color: $accent-color
          .item-properties
            max-height: 300px
.questions
  &__title
    text-align: left
  &__menu
    display: flex
    flex-wrap: wrap
    margin: 10px 0
    @media (min-width: 1200px)
      margin: 20px 0
    .question-type-link
      display: inline-block
      margin-left: 15px
      color: #999
      border-bottom: 1px solid transparent
      transition: color .3s, border-color .3s
      &:nth-of-type(1)
        margin-left: 0
      &.active
        color: $txt-color
        border-color: $accent-color
  &__content
    &__tab
      .addlink
        @media (min-width: 414px)
          display: inline-block
          width: auto
          padding: 10px 25px
      .photo-block
        position: relative
        padding: 0 30px
        margin-bottom: 20px
        &__title
          color: #999
          display: block
          margin-bottom: 10px
        .owl-nav
          display: flex
          align-items: center
          justify-content: space-between
          position: absolute
          top: 50%
          left: 0
          width: 100%
          height: 1px
          font-size: 18px
          line-height: 20px
      .tab-content
        margin-top: 15px
        .product-review-reviews-navigation
          display: flex
          flex-wrap: wrap
          justify-content: space-between
          border: 1px solid #eee
          padding: 10px
          font-size: 15px
          line-height: 20px
        .sort-dropdown
          display: flex
          .ut2-sort-label
            white-space: nowrap
          .cm-combination
            display: inline-block
            margin-left: 10px
            color: $accent-color
        .product-review-view__posts
          margin-top: 15px
          .product-review-post
            margin-bottom: 25px
          .product-review-post-customer
            display: flex
            justify-content: space-between
            &__address
              display: flex
              flex-wrap: wrap
              align-items: center
              justify-content: flex-start
              font-style: normal
              .review-stars
                display: inline-flex
                align-items: center
                justify-content: flex-start
                margin-right: 10px
                color: #ffcb00
                font-size: 18px
                line-height: 20px
                .icon
                  margin-right: 5px
            &__name
              font-size: 18px
              line-height: 18px
              font-weight: 700
            &__date
              font-size: 16px
              line-height: 20px
              color: #999
        .product-review-post__content
          margin-top: 10px
